'use strict';
define('app/core',['templates/sample'], function (template) {
  var $, $info, d;
  d = document;
  $ = function (el) {
    return d.querySelector(el);
  };
  $info = $('#info');
  $info.insertAdjacentHTML('afterbegin', template({
    name: navigator.appName,
    version: navigator.appVersion
  }));
});
